.home{

    #page{
        overflow-x:hidden;
    }

    .hero-container{
        width:100%;
        height:500px;
        position:relative;
            .animation-paragraph-container{
                position:absolute;
                top:8rem;
                z-index:10;
            }
    }

    .hero-gradient{
        position: absolute;
        width: 100%;
        height: 100%;
        background: #000;
        opacity: 0.5;
    }

    .video-background{
        height:500px;
    }

    .menu-toggle{
        display:none;
    }


    .edward-header{
        color:#fff;
    }

    //Heading Animation

    .header-animation{
        position:absolute;
        width: 100%;
        margin: auto;
        text-align: center;
        perspective: 800px;
        min-width: 320px;
        margin-top: 4em;
        z-index:10;
    }

    span {
        display: inline-block;
        color: #fff;
        font-size: 2rem;
        transform-origin: 50% 70%;
    }

    br {
        display: none;
    }


/* Every second span */

    span:nth-child(2n) {
        color: #fff;
    }

    span:nth-child(1) {
        animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) .2s both;
    }

    span:nth-child(2) {
        animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55).4s both;
        padding-right:20px;
    }

    span:nth-child(3) {
        animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55).6s both;
    }

    span:nth-child(4) {
        animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) .8s both;
        padding-right:20px;
    }

    span:nth-child(5) {
        animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) 1s both;
        display: inline-block;
    }

    span:nth-child(6) {
    animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) 1.2s both;
    }

    span:nth-child(7) {
    animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) 1.4s both;
    }

    span:nth-child(8) {
    animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) 1.6s both;
    }

    span:nth-child(9) {
    animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) 1.6s both;
    }

    span:nth-child(10) {
    animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) 1.8s both;
    }

    span:nth-child(11) {
    animation: flipUp 1s cubic-bezier(0.68, -0.55, 0.26, 1.55) 2s both;
    }


    /* ANIMATION */

    @keyframes flipUp {
    from {
        transform: rotateX(90deg);
    }
    to {
        transform: rotateX(0deg);
    }
    }


    /* RESPONSIVE STUFF */

    @media screen and (min-width: 400px) {
    span {
        font-size: 2.5rem;
    }
    span:nth-child(5) {
        margin: 0;
    }
    }

    @media screen and (min-width: 600px) {
    span {
        font-size: 5em;
    }
    span:nth-child(5) {
        margin: 0;
    }
    }

    @media screen and (min-width: 916px) {
    span {
        font-size: 6.2em;
    }
    span:nth-child(5) {
        display: inline-block;
        margin: 0;
        
    }
    }

    //Text-animation
    .page-hero {
        height: 200px;
        overflow: hidden;
        position: absolute;
        width: 100%;
        top: 8rem;
        z-index:10;
}

    .page-hero h1 {
        animation:
        slideUp 0.75s 3s cubic-bezier(0.17,.88,.32,1.27) both,
        fadeIn .25s .5s ease-in both;
        padding: 0 20px;
        position: absolute;
        text-align: center;
        top: 50%;
        width: 100%;
        color:#fff;
    }

    @keyframes slideUp {
        from {transform: translateY(200%);}
        to {transform:translateY(-50%);}
    }

    @keyframes fadeIn {
        from {opacity: 0;}
        to {opacity: 1;}
    }

    .nav-list{
        list-style:none;
        padding:0;
        position: absolute;
        bottom: 0;
        display: flex;
        width: 100%;
        justify-content: space-around;
    }

//About Section

    .section-heading{
        margin-left:10%;
    }

    //Header

    @import url(https://fonts.googleapis.com/css?family=Open+Sans);

h1 {
  width: 100%;
  text-align: center;
}
body {
  margin: 0;
}

* {
  font-family: "Open Sans";
  box-sizing: border-box;
}

.top-nav {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  background-color: #00BAF0;
  background: linear-gradient(to left, #f46b45 , #eea849); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

  color: #FFF;
  height: 50px;
  padding: 1em;
}

.menu {
  display: flex;
  flex-direction: row;
  list-style-type: none; 
  margin : 0;
  padding: 0;
}

.menu > li {
  margin: 0 1rem;
}

.menu-button-container {
  display:none;
  height: 100%;
  width: 30px;
  cursor: pointer;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#menu-toggle {
  display: none;
}

.menu-button, .menu-button::before, .menu-button::after {
  display: block;
  background-color: #fff;
  position: absolute;
  height: 4px;
  width: 30px;
  transition: transform 400ms cubic-bezier(0.23, 1, 0.32, 1);
  border-radius: 2px;
}

.menu-button::before {
  content: '';
  margin-top: -8px;
}

.menu-button::after {
  content: '';
  margin-top: 8px;
}

#menu-toggle:checked + .menu-button-container .menu-button::before {
  margin-top: 0px;
  transform: rotate(405deg);
}

#menu-toggle:checked + .menu-button-container .menu-button {
  background: rgba(255, 255, 255, 0); 
}

#menu-toggle:checked + .menu-button-container .menu-button::after {
  margin-top:0px;
  transform: rotate(-405deg);
}


@media (max-width: 700px){
  .menu-button-container {
      display: flex;
  }
.menu {
  position: absolute;
  top: 0;
  margin-top: 50px;
  left: 0;
  flex-direction: column;
  width: 100%;
  justify-content: center;
  align-items: center;
}

#menu-toggle ~ .menu li{
  height: 0;
  margin: 0;
  padding: 0;
  border: 0;
  transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
}

#menu-toggle:checked ~ .menu li{
  border: 1px solid #333;
  height: 2.5em;
  padding: 0.5em;
  transition: height 400ms cubic-bezier(0.23, 1, 0.32, 1);
}

.menu > li {
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0.5em 0;
  width: 100%;
  color: white;
  background-color: #222;
}

.menu > li:not(:last-child){
  border-bottom: 1px solid #444;
}

}

    

}//Home Wrapper